.th-15{
    width: 15%;
}
.th-20{
    width: 15%;
}
.th-10{
    width: 15%;
}
.th-25{
    width: 15%;
}
.thankyou-banner{
    text-align: center;
    .img{
        @extend %result;
        width: 200px;
        height: 190px;
        margin: 0 auto;
        background-size: 100% 100%;
    }
}

.thankyou-content{
    margin:40px auto 30px;
    h3,h2,h4{
        font-weight: 500;
    }
    p{
        font-size: 1em;
    }
}
.cst-chart-wrapper{
    position:relative;
    padding-top: 40px;
    padding-top: 30px;
    padding-left: 15px;
    padding-right: 15px;   
    // & .table-div{
    //     width:75%; 
    //     &.full{
    //         display:block;           
    //         margin:0px;
    //     }
    //     &.full{
    //         width:100%;
    //     }
    // }
    // & .kta-div{
    //     width:25%;   
    // }

    & .avg-data{
        height:20px;
        background-color: $sky-clr;
    }

    & .your-data{
        height:20px;
        background-color: #AAE6F1;
    }
    .result-info{
        height: calc(100vh - 190px);
        padding-left: 15px;
        padding-right: 15px;
        // overflow-y: auto;
        @media (max-width:1024px){
            padding-left: 0;
            padding-right: 0;
        }
    } 
    .markings{
        float: right;
        margin-bottom: 15px;
        .avg, .sel{
            display: inline-block;
           span{
            display: inline-block;          
            font-size: 1em;
            color: $bl-39;
           }
        }
        .avg{
          margin-right: 10px;
          &> span{          
              &.mark-clr{
                background-color: $sky-clr;
                width: 24px;
                height: 8px;
                margin-right: 10px;
              }
            
          }
        }
        .sel{
            &> span{
                &.mark-clr{
                    background-color: $sky-light;
                width: 24px;
                height: 8px;
                margin-right: 10px;
                }
              
            }
        }
    }  
    .result-move{
        margin-bottom: 20px;
        padding-left: 30px;
        padding-right: 30px;
        @media (max-width:1024px){
            padding-left: 15px;
            padding-right:15px;
        }
        @media (max-width:480px){
            padding-left: 0;
            padding-right: 0;
        }
    }
}
.tw-key{
    @media (max-width:767px){        
            width: 900px;      
        }
    .half-fl, .half-fl{
        float: left;
        padding-left: 15px;
        padding-right: 15px;
    }
    .tabl-head{
        width: 100%;
        background-color: $f6-clr;
        padding: 10px;
        margin-bottom: 15px;
    }
    table{
        width: 100%;
    }
    .kta-div{
        width:25%;
        .kta-inner{
            background-color: $f6-clr;
            padding: 15px;
            font-size: 1em;
              .lbl{
                  color:$bl-39;
                  font-weight: bold;
                  @media (max-width:1024px){
                    font-size: 0.85em;
                  }
              }
            .key-info{
                color:$bl-39;
                margin-top: 10px;
                margin-bottom: 10px;
                @media (max-width:1024px){
                    font-size: 0.90em;
                }
            }
        }
      
        & > .lbl-wt{
            font-weight: 1em;
            color: $bl-39;
        }
    }
    table{
        width:100%;
        border-collapse: collapse;
        border-spacing: 0;
        color: $bl-39;
        thead{
          &> tr {
              th{
                  font-size: 1em;
                  font-weight: bold;
                  padding: 10px;
                  border-bottom: 2px solid #E1DFDF;
                  &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6){
                      text-align: right;
                      padding-left: 0;
                      padding-right: 0;
                      width: 10%;
                  }
                @media (max-width:1024px){
                    font-size: 0.85em;
                }
                 
              }
          }
         }
         tbody{
             & > tr{
                 td{
                    font-size: 1em;
                    padding: 10px;
                    border-bottom: 1px solid #E1DFDF;
                   &.w10{                       
                        padding-left: 0!important;
                        padding-right: 0!important;                        
                    }  
                    @media (min-width:1024px){
                        font-size: 0.90em;
                    }                
                 }
                 &:not(:last-child){
                     td{
                        &.w10{
                            border-bottom: transparent;
                        }  
                     }
                 }
             }
          
         }
         th, td{
             text-align: left;
         }
    }
    &:last-child{
        margin-top: 20px;
    }
    .significant{
        border: 1px solid $light-bdr;
        font-size: 0.9em;
        color: $bl-39;
        padding: 15px;
        margin-top: 30px;
        margin-bottom: 15px;
    }
    .nob {
        width: 400%;
        height: 15px;   
        position: relative;
        z-index: 1;
        margin-bottom: 1px;
        &:nth-child(1){
            background: $sky-clr;
        }
        &:nth-child(2){
            background:$sky-light;
        }
    }
    
}
.bdr-lf{
    border-left: 1px solid $light-bdr;
    padding-left: 0!important;
    padding-right: 0!important;  
   
}
.bdr-rt{
    border-right: 1px solid $light-bdr;
}
.txt-rt{
    text-align: right!important;
    padding-right: 0!important;
}

.table-div{
    width:75%; 
    &.full{
        display:block;           
        margin:0px;
    }
    &.full{
        width:100%;
    }
}

